/* === Base (Light Mode) === */
:root {
  /* === Brand Colors === */
  --color-primary: #E5B100; /* Imperial Gold */
  --color-secondary: #FFB300; /* Amber Alloy */
  --color-accent: #FF6B00; /* Molten Orange */

  /* === Backgrounds === */
  --color-bg: #F7F7F7; /* Ash White */
  --color-surface: #FFFFFF;
  --color-bg-gradient: linear-gradient(135deg, #E5B100 0%, #FF6B00 100%);

  /* === Text === */
  --color-text: #1A1A1A; /* Graphite Grey */
  --color-text-muted: #929292;
  --color-text-inverse: #FDFCF8;
  --color-text-inverse-fade: #fdfcf854;  /* Soft Ivory */

  /* === Borders & Shadows === */
  --color-border: #E0E0E0;
  --color-shadow: rgba(0, 0, 0, 0.1);

  /* === Components === */
  --color-button-bg: var(--color-primary);
  --color-button-text: var(--color-text);
  --color-button-hover: #FFB300;

  /* === Typography === */
  --font-heading: 'Montserrat', sans-serif;
  --font-body: 'Inter', sans-serif;

  /* === Radius === */
  --radius-base: 8px;
  --radius-lg: 16px;
  --radius-xl: 24px;
}

/* === ðŸŒ‘ Dark Mode (Automatic: system preference) === */
@media (prefers-color-scheme: dark) {
  :root {
    --color-bg: #0D0D0D; /* Obsidian Black */
    --color-surface: #1A1A1A;
    --color-bg-gradient: linear-gradient(135deg, #E5B100 0%, #FF6B00 100%);

    --color-text: #FDFCF8; /* Soft Ivory */
    --color-text-muted: #BDBDBD;
    --color-text-inverse: #1A1A1A;
    --color-text-inverse-fade: #1a1a1a4d;

    --color-border: #3C3C3C;
    --color-shadow: rgba(0, 0, 0, 0.25);

    --color-button-bg: var(--color-primary);
    --color-button-text: #0D0D0D;
    --color-button-hover: var(--color-accent);
  }
}

/* === ðŸŒ“ Optional: Manual Toggle via Class === */
.dark-mode {
  --color-bg: #0D0D0D;
  --color-surface: #1A1A1A;
  --color-text: #FDFCF8;
  --color-text-muted: #BDBDBD;
  --color-text-inverse: #0c0c0c; 
  --color-border: #3C3C3C;

  --color-button-bg: var(--color-primary);
  --color-button-text: #0D0D0D;
  --color-button-hover: var(--color-accent);
}


*{
  padding: 0%;
  margin:0 ;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  outline: none;
  border: none;
  color: var(--color-text);
}
*:not(a){
  text-decoration: none;
}
body, html {
  overflow-x: hidden;
  margin: 0;
  font-family: poppins-regular, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-bg);
}
header{
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo{
  font-family: didot, serif;
  font-size: 70px;
  padding: 10px;
  background-image:linear-gradient(90deg, var(--color-text) 0%, var(--color-text) 50%, var(--color-primary) 50%) ;
  background-size: 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: logo-anim .5s ease 1s forwards;
  background-position: right;
  position: relative;  
  text-align: center;
}
.credit{
  font-size: 12pt;
  color: var(--color-text-muted);
  font-family: poppins-regular;
  z-index: 5;
  margin: 0;
  padding: 0;
}
.credit a{
  text-decoration: underline !important;
  color: var(--color-accent) !important;
}
nav{
  background-color: var(--color-primary);
  width: 100%;
  height: 60px;
  min-height: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-btn, .search-btn{
  width: 40px;
  height: 40px;
  border: 1px solid green;
  display: none;
}
nav a{
  color: var(--color-text-inverse);
  font-family: poppins-semibold;
  font-size: 14pt;

}
nav ul{
  display: flex;
  justify-content: space-around;
  width: 75%;
}

.hero-sec{
