  height: 100vh;
  min-height:400px ;
  max-height: 500px ;
  display: flex;
  align-items: center;
  margin-top: 20px;
  justify-content: center;
  gap: 100px;
  background-image: var(--color-bg-gradient);
  position: relative;
  padding: 40px 50px;
}
.hero-sec::before{
  display: none;
  content: '';
  position: absolute;
  width: 100%;
  height: 60px;
  top: 0;
}
.intro-visual{
  text-align: center;
}
.iv{
  color: var(--color-secondary);
  font-family: poppins-black;
  font-size: 70px;
}
.iv-fade{
  scale: .9;
  color:  #ffb30082 ;
}
.iv-fade2{
  scale: .7;
  color:  #ffb30062 ;
}
.discipline-iv{
  translate: 0 30px;
}
.focus-iv {
  translate: 0 60px;
}
.resilience-iv {
  translate: 0 -30px;
}
.growth-iv {
  translate: 0 -60px;
}
.introtxt{
  
  width: fit-content;
  border: 1px solid transparent ;
  color: var(--color-text-inverse);
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 10px;
}
.introtxt *{
  color: inherit;
  text-align: right;
}
.introtxt h1{
  font-size: 30pt;
  font-family: poppins-semibold;
}
.introtxt button{
  padding: 10px 20px;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  border: 3px solid white; 
  font-size: 15pt;
  font-family:poppins-semibold ;
  border-radius: 3px;
  color: var(--color-text);
  cursor:pointer;
  transition: background-color .4s ease, scale .4s ease, color .4s ease;
}
.introtxt button:hover{
  background-color: var(--color-text);
  scale: 1.03;
