  color: var(--color-text-inverse);
}
.hero-cta{
  font-size: 18pt;
  font-family: poppins-semibold;
  margin-top: 10px;
}





.articles-sec{
  width: 100%;
  height: fit-content;
  padding: 10px 20px;
  display: flex;
  margin-top: 30px;
  flex-direction: column;
  justify-content: space-between;
}
.article-con{
  width: 100%;
  display: grid;
  grid-template-columns: 7fr 9fr 4fr;
  gap: 30px;
}
.article-grid{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  grid-auto-rows: auto;
  grid-gap: 30px;
  margin: 0 10px;
}
.article-card{
 height: fit-content;
 transition:scale .4s ease, box-shadow .4s ease;
 padding: 15px;
 border-radius: var(--radius-base);
}
.article-card:hover, .featured-article-card:hover{
  scale: 1.03;
  box-shadow: 0px 0px 20px #ffb30078;
}
.article-card .title, .trending-article-card .title, .featured-article-card .title{
  font-family: didot, serif;
  color: var(--color-primary);
  font-size: 17pt;
  line-height: 30px;
  text-shadow: 0px 0px 4px 0px var(--color-text);
}
.article-card .category, .trending-article-card .category, .featured-article-card .category{
  font-family: poppins-semibold;
  color: var(--color-text);
  margin-bottom: 5px;
  position: relative;
  width: fit-content;
  display: block;
}
.article-card .category::after, .trending-article-card .category::after, .featured-article-card .category::after{
  content: '';
  width: 70%;
  height: 3px;
  border-radius: 1px;
  top: 100%;
  left: 0;
  background-color: var(--color-accent);
  position: absolute;
}
.article-card .img-con{
  width: 70%;
  height: auto;
  padding-right: 2em;
}
.article-card .img-con img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.trending-article-grid{
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: auto;
  height: fit-content;
  gap: 15px;
}
.trending-article-card{
  padding: 10px;
  transition: all .4s ease;

}
.trending-article-card:hover{
  translate: -20px 0;
  scale: 1.03;
  box-shadow: 20px 0px 10px #ffb30033;
}
.featured-article-card{
  padding: 15px;
  transition: scale .4s ease;
}
.featured-article-card .img-con{
  width: 70%;
  height: auto;
  min-height: 200px;
}
.featured-article-card .img-con img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.featured-article-card .title{
  font-size: 35pt;
  line-height: inherit;
}
.featured-article-card .category{
  font-size: 15pt;
}
.featured-article-card .author{
  font-size: 15pt;
}


.podcasts-sec{
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 60px 18px;
  background-image: linear-gradient(45deg, var(--color-text), var(--color-primary), var(--color-text));
  color: var(--color-text-inverse);
  position: relative;
  margin-bottom: 300px;
}
.podcasts-sec *{
  color: inherit;
  position: relative;
  z-index: 3;
}

.podcast-con{
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr ) );
  place-items: center;
  gap: 50px;
  padding: 50px;
}
.podcast-card{
  min-width: 250px;
  width: 100%;
  text-align: center;
  padding: 30px;
  background-color: var(--color-text-inverse-fade);
  border-radius: var(--radius-base);
  color: var(--color-text);
  backdrop-filter: blur(5px);
  transition: background-color .4s ease, scale .4s ease;
}
.podcast-card *{
  color: inherit;
}
.podcast-card .img-con{
  width: 100%;
  aspect-ratio: 5/3;
  overflow: hidden;
  border-radius: var(--radius-base);
  transition: scale .4s ease;
  position: relative;
}
.podcast-card .img-con::after{
  position: absolute;
  content: 'â–·';
  width: 80px;
  aspect-ratio: 1/1;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  z-index: inherit;
  border-radius: 50%;
  display: grid;
  place-items: center;
  font-size: 30pt;
  padding-left: 10px;
  color: #1A1A1A;
  opacity: 0;
  scale: .8;
  transition: all .4s ease;
}
.podcast-card .img-con:hover::after{
  opacity: 1;
  scale: 1.1;
}
.podcast-card .img-con img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.podcast-card:hover{
  background-color: var(--color-bg);
  scale: 1.02;
}
.podcast-card:hover .img-con{
  scale: 1.02;
}

.podcast-img{
  position: absolute;
  z-index: 0;
  left: 50%;
  translate: -50% 0;
  top: 0;
  width:950px;
  height: 950px;
  overflow-y: hidden;
  overflow-x: hidden;
  
}
.podcast-img::after{
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-image: linear-gradient( transparent , var(--color-bg));
  z-index: 3;
}
.podcast-img img{
  width: 100%;
  height: auto;
}

