@media screen and (max-width : 1030px ){
  .introtxt button{
    font-size:10pt ;
  }
  .introtxt h1{
    font-size: 25pt;
  }
  .introtxt p{
    font-size: 9pt;
  }
  .iv{
    font-size: 60px;
  }

}
@media screen and (max-width : 986px ){
    .podcast-img{
        height: 1100px;
        translate: -50% 230px;
    }
    .podcast-card{
        max-width: 500px;
        text-align: left;
    }
    .podcast-card:nth-child(1){
        margin-right: auto;
    }
    .podcast-card:nth-child(2n){
        margin-left: auto;
        text-align: right;
    }
}
@media screen and (max-width : 890px ){
  .article-con{
    grid-template-columns: 1fr;
  }
  .trending-article-card:hover{
    translate: 0 -5px;
    scale: 1.03;
    box-shadow:0px 5px 10px #ffb30033;
  }
  .trending-article-card{
    width: fit-content;
  }
  .iv{
    font-size: 50px;    
    }
    .hero-sec{
        gap: 50px;
    }
  .masterclass{
    width: 40%;
  }
}
@media screen and (max-width : 750px ){
    .hero-sec{
        flex-direction: column;
        max-height: none;
        height: fit-content;
        gap: 0px;
        padding-top: 0;
    }
    .hero-sec *{
        text-align: center;
    }
    .introtxt{
        align-items: center;
    }
    .links-grid{
        grid-template-columns: 1fr 1fr;
    }
    .masterclass{
        width: 50%;
    }
    .footer-info h1{
        font-size: 50pt;
    }
    .footer-info p{
        font-size: 9pt;
    }
    .challenge-sec{
        flex-direction: column-reverse;
        justify-content: center;
    }
    .challenge-content{
        width: 100%;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .challenge-img{
        display: none;
    }
    nav ul a{
      font-size: 9pt;
    }
}
@media screen and (max-width : 600px ){
  .logo{
    font-size: 45pt;
  }
  .podcast-card{
        max-width: 500px;
        text-align: center;
    }
  .podcast-card:nth-child(1){
        margin-right: none;
    }
    .podcast-card:nth-child(2n){
        margin-left: none;
        text-align: center;
    }
    .footer{
        flex-direction: column-reverse;
        align-items: center;
    }
    .masterclass{
        margin-left: 0;
        border: 0px;
        width: 75%;
    }
    .links-grid{
        grid-template-columns: repeat(3, 1fr);
    }
    .community-sec{
        flex-direction: column;
    }
    .community-sec *{
        width: 100%;
    }
    nav ul{
      width: 90%;
    }
   
}
@media screen and (max-width : 530px){
    .podcast-con{
        padding: 0;
        margin-top: 30px;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr ) );
    }
}
    
@media screen and (max-width : 430px ){
  .article-card .title{
    font-size: 12pt;
  }
  .article-card .category{
    font-size: 8pt;
  }
  .article-card .author{
    font-size: 8pt;
  }
  .masterclass{
    width: 100%;
  }
  .links-grid{
    grid-template-columns: 1fr ;
    place-items: center;
  }
  .footer *{
    text-align: center;
  }
  .socials{
    justify-content: center;
  }
  .footer-info h1{
    font-size: 45pt;
  }
  .podcast-card .topic{
    font-size: 15pt;
  }
  .podcast-card .category{
    font-size: 9pt;
  }
  .podcast-card .author{
    font: size 9pt;
  }
  .podcast-img{
    width: 600px;
  }
  .podcast-img::after{
    display: none;
  }

  .challenge-btn-con button{
    padding: 8px 17px;
    font-size: 12pt;
}
  nav ul{
      width: 100%;
  }
  nav ul a{
      font-size: 8pt;
  }
  .article-card .img-con{
    width: 100%;
    padding-right:0 ;
  }
  .article-card .title{
    font-size: 10pt;
    line-height:20px ;
  }
  .featured-article-card .img-con{
    width: 100%;
  }
  .links-grid{
    grid-gap: 5px;
  }
}
@media screen and (max-width : 350px) {
  .hero-sec{
    padding-left: 0 ;
    padding-right: 0 ;
  }
}


